ansform:rotateZ(180deg)}.course__trigger.is-disabled{opacity:0.5}.Collapsible__trigger{display:block;font-weight:400;text-decoration:none;position:relative;padding:10px}.Collapsible__trigger:after{font-family:'FontAwesome';content:'\f107';position:absolute;right:10px;top:10px;display:block;transition:transform 300ms}.Collapsible__trigger.is-open:after{transform:rotateZ(180deg)}.Collapsible__trigger.is-disabled{opacity:0.5}.secondtext li{font-size:20px}.Resume{display:flex;justify-content:center;flex-direction:column;align-items:center;font-size:20px}.react-pdf__Page{margin-top:20px}.react-pdf__Page__canvas{min-height:80vh !important;max-width:80vw !important}hr{border-top:2px dotted #3f51b5}.new{padding-left:20px;font-size:18px}.new li{list-style-type:none;padding-top:10px}@media (prefers-color-scheme: dark){:root{--background-color: #1e1f23;--secondary-color: #232428;--text-dark: #efefef;--text: #c4c5c9;--text-light: #6c6d71;--text-lighter: #8e8f93}}

